<template>
<div class="fn-clear">
    <section class="section-wrap-2">
        <c-title title="最新资讯" engTitle="News"></c-title>
        <div class="list-wrap">
            <ul class="info-list">
                <li class="item" v-for="(item, index) in newsList" :key="index">
                    <div class="whole fn-clear">
                        <div class="part left">
                            <img :src="news1">
                        </div>
                        <div class="part right">
                            <div>
                                <span class="label-primary">公司新闻</span>
                                <p>
                                    宝信软件成功签约龙山县智慧农业项目{{index}}
                                </p>
                                <span class="time">
                                    2017.05.10
                                </span>
                            </div>
                        </div>
                    </div>
                </li>
                <li class="last">
                    <div class="content">
                        <p>
                            READ MORE
                        </p>
                        <span class="arrow"><i class="iconfont icon-jiantou"></i></span>
                    </div>
                </li>
            </ul>

            <el-carousel ref="newsSlide" indicator-position="none" :autoplay="false" arrow="never" class="info-list-move" v-move="move">
                <el-carousel-item v-for="(item, index) in newsList" :key="index" class="item">
                    <div class="whole fn-clear">
                        <div class="part left">
                            <img :src="news1">
                        </div>
                        <div class="part right">
                            <div>
                                <span class="label-primary">公司新闻</span>
                                <p>
                                    宝信软件成功签约龙山县智慧农业项目{{index}}
                                </p>
                                <span class="time">
                                    2017.05.10
                                </span>
                            </div>
                        </div>
                    </div>
                </el-carousel-item>
                <el-carousel-item :key="newsList.length" class="last">
                    <div class="content">
                        <p>
                            READ MORE
                        </p>
                        <span class="arrow"><i class="iconfont icon-jiantou"></i></span>
                    </div>
                </el-carousel-item>
            </el-carousel>
        </div>
    </section>
</div>
</template>
<script>
import cTitle from './cTitle'
import news1 from '../assets/news1.png'
import move from '../directive/move'
export default {
    data() {
        return {
            news1,
            activeIndex : 0,
            newsList    : [{
                title   : '宝信软件成功签约龙山县智慧农业项目',
                date    : '2017.05.10',
                img     : news1
            }, {
                title   : '宝信软件成功签约龙山县智慧农业项目',
                date    : '2017.05.10',
                img     : news1
            }, {
                title   : '宝信软件成功签约龙山县智慧农业项目',
                date    : '2017.05.10',
                img     : news1
            }, {
                title   : '宝信软件成功签约龙山县智慧农业项目',
                date    : '2017.05.10',
                img     : news1
            }, {
                title   : '宝信软件成功签约龙山县智慧农业项目',
                date    : '2017.05.10',
                img     : news1
            }]
        }
    },
    methods: {
        next() {
            this.$refs.newsSlide.next()
        },
        prev() {
            this.$refs.newsSlide.prev()
        },
        move(type) {
            if (type === 'right') {
                this.prev()
            }
            if (type === 'left') {
                this.next()
            }
        }
    },
    components: {
        cTitle
    },
    directives: {
        move
    }
}
</script>
<style lang="scss" scope>
@import '../style/base/param';
.info-list {
    margin-left: -20px;
    margin-right: -20px;
    margin-bottom: 0;
    margin-top: 0;
    .item:nth-child(1)  {
        margin-top: 0;
    }
    .item:nth-child(2) {
        margin-top: 0;
    }
    .item, .last {
        float: left;
        position: relative;
        width: calc(50% - 40px);
        min-width: 440px;
        margin-top: 40px;
        margin-left: 20px;
        margin-right: 20px;
    }
    .item {
        background: $blue;
        cursor: pointer;
        .whole {
            transition: all 0.3s;
            &:hover {
                transform: translate(-8px, -8px)
            }
        }
    }
    .last {
        background: $white;
        vertical-align: middle;
        cursor: pointer;
        transition: all 0.3s;
        &:hover {
            color: $white;
            background: $blue;
        }
        .content {
            position: relative;
            padding: 16.5% 0;
        }
        .content > * {
            position: absolute;
            top: 50%;
        }
        p {
            width: 100%;
            text-align: center;
            display: inline-block;
            margin: -1rem 0 0 0;
            font-size: 2.4rem
        }
        .arrow {
            display: inline-block;
            margin-top: -2.5rem;
            right: 40px;
            i.iconfont.icon-jiantou {
                font-size: 4.8rem;
            }
        }
    }
    .part {
        float: left;
        width: 50%;
        position: relative;
        padding: 16.5% 0;
        overflow: hidden;
        font-family: $fontlight;
        color: $blue;
        &.right {
            background: $white;
            & > div {
                padding: 20px;
                bottom: 0;
            }
        }
        & > * {
            position: absolute;
            top: 0;
            left: 0;
        }
        p {
            font-size: 1.6rem;
        }
        img {
            width: 100%;
        }
    }
}
.info-list-move {
    display: none;
    position: relative;
    padding: 20.5% 0;
    & > .el-carousel__container {
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
    }
    .item, .last {
        width: 100%;
    }
    .item {
        background: $blue;
        .part {
            float: left;
            width: 50%;
            position: relative;
            padding: 20.5% 0;
            overflow: hidden;
            font-family: $fontlight;
            color: $blue;
            &.right {
                background: $white;
                font-size: 1.2rem;
                & > div {
                    padding: 20px;
                    bottom: 0;
                }
            }
            & > * {
                position: absolute;
                top: 0;
                left: 0;
            }
            p {
                font-size: 1.44rem;
            }
            img {
                width: 100%;
            }
        }
    }
    .last {
        background: $white;
        vertical-align: middle;
        transition: all 0.3s;
        &:active {
            color: $blue;
        }
        .content {
            position: relative;
            padding: 20.5% 0;
        }
        .content > * {
            position: absolute;
            top: 50%;
        }
        p {
            width: 100%;
            text-align: center;
            display: inline-block;
            margin: -1rem 0 0 0;
            font-size: 2.4rem
        }
        .arrow {
            display: inline-block;
            margin-top: -2.5rem;
            right: 40px;
            i.iconfont.icon-jiantou {
                font-size: 4.8rem;
            }
        }
    }
}
.time {
    color: $blue;
}
@media only screen and (max-width: 768px) {
    ::-webkit-scrollbar {
        display: none;
    }
    .list-wrap {
        margin-top: -10px;
        padding-top: 10px;
        margin-left: -10px;
        padding-left: 10px;
        overflow-y: scroll;
    }
    .info-list {
        display: none;
        margin-left: 0;
        margin-right: 0;
        .item, .last {
            width: 100%;
            min-width: initial;
            margin-top: 0;
            margin-left: 0;
            margin-right: 0;
        }
        .last {
            .content {
                padding: 20.5% 0;
            }
        }
        .part {
            padding: 20.5% 0;
        }
        .item .whole:hover {
            transform: none;
        }
        .item .whole:active {
            transform: translate(-8px, -8px);
        }
        .last:hover {
            color: inherit;
            background: $white;
        }
        .last:active {
            color: $white;
            background: $blue;
        }
        .part.right {
            font-size: 1.2rem;
            p {
                font-size: 1.44rem;
            }
        }
    }
    .info-list-move {
        display: block;
    }
}
@media only screen and (min-width: 768px) {

}
@media only screen and (min-width: 992px) {

}
@media only screen and (min-width: 1200px) {
    .info-list {
        .part.right {
            p {
                font-size: 2.0rem;
            }
        }
    }
}
@media only screen and (min-width: 1800px) {
    .info-list {
        .part.right {
            p {
                font-size: 2.4rem;
            }
            & > div {
                padding: 60px 40px;
            }
        }
        .last .arrow {
            right: 80px;
        }
    }
}
</style>



